.press-footer {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;

  .press-footer__main {
    flex: 1 0 auto;
  }

  .press-footer__bottom {
    flex: 0 0 auto;
  }
}

.container {
  width: calc(100% - 1.833rem);
  max-width: 640px;
  margin: 0 auto;
}


