*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  scrollbar-width: thin;
  scrollbar-color: $blue $green;

  &::-webkit-scrollbar {
    width: rem(6);
  }

  &::-webkit-scrollbar-track {
    background: $green;
  }

  &::-webkit-scrollbar-thumb {
    background-color: var(--blue);
    border-radius: rem(10);
    border: rem(1) solid black;
  }
}

html {
  -webkit-font-smoothing: antialiased;
  -webkit-backface-visibility: hidden;
  -webkit-user-drag: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-overflow-scrolling: touch;
  -webkit-touch-callout: none;
  -webkit-text-size-adjust: 100%;
}

html {
  font-size: 9px;

  @media only screen and (min-width: 320px) and (min-height: 481px) and (max-height: 533px) {
    font-size: 10px;
  }

  @media only screen and (min-width: 320px) and (min-height: 534px) and (max-height: 658px) {
    font-size: 11px;
  }

  @media only screen and (min-width: 360px) /*and (max-height: 670px)*/ {
    font-size: 12px;
  }

  @media only screen and (min-width: 414px) /*and (max-height: 670px)*/ {
    font-size: 13.5px;
  }

  @media screen and (orientation:landscape) {
    font-size: 13px;
  }
}

body, html {
  height: 100%;
  margin: 0;
  background: none;
  min-height: 100vh;
}

body {
  @include font-style(rem(12), $font-normal, 1.1, $black);
  font-family: $font-family;
  word-wrap: break-word;
  background-color: $white;
  padding: 0;
  width: 100%;
  position: relative;
  font-kerning: none;
  font-smooth: always;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  overflow-anchor: auto;
  overflow-x: hidden;

  overscroll-behavior-y: contain;
}

@mixin iphonex-css {
  padding-top: constant (safe-area-inset-top); // высота панели навигации + строка состояния 88 пикселей
  padding-top: env (safe-area-inset-top); // высота панели навигации + строка состояния 88 пикселей
  padding-left: constant (safe-area-inset-left); // Если экран не вертикальный, он равен 0
  padding-left: env (safe-area-inset-left); // Если экран не вертикальный, он равен 0
  padding-right: constant (safe-area-inset-right); // Если экран не вертикальный, он равен 0
  padding-right: env (safe-area-inset-right); // Если экран не вертикальный, он равен 0
  padding-bottom: constant (safe-area-inset-bottom); // высота нижней дуги 34px
  padding-bottom: env (safe-area-inset-bottom); // высота нижней дуги 34px
}

@media only screen and (device-width: 375px) and (device-height: 812px) and (-webkit-device-pixel-ratio: 3) {
  body {
    @include iphonex-css;
  }
}

///////

img {
  font-size: rem(14);
  position: relative;
  max-width: 100%;
  height: auto;
  border: 0;
  vertical-align: middle;
  text-align: center;

  /*&:before {
    content: "We're sorry, the image below is broken :(";
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $secondary;
    padding: rem(10);
    height: 100%;
    width: 100%;
  }*/
}

ul {
  padding: 0;
  margin: 0;
}

.hidden {
  display: none;
}

.screen-reader-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

///////

article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0 !important;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

abbr[title],
abbr[data-original-title] {
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
  text-decoration-skip-ink: none;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 0;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: 500;
}

dd {
  margin-bottom: 0.5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

a {
  color: $blue;
  text-decoration: none;
  background-color: transparent;
}

a:not([href]):not([class]) {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([class]):hover {
  color: inherit;
  text-decoration: none;
}

pre,
code,
kbd,
samp {
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 1em;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
  -ms-overflow-style: scrollbar;
}

figure {
  margin: 0 0 1rem;
}

svg {
  overflow: hidden;
  vertical-align: middle;
}
.p-sticky {
  position: sticky;
  top: 0;
  z-index: 2;
}

.table {
  border-spacing: 0;
  text-align: center;
  width: 100%;

  td, th {
    padding: rem(5);
  }
}

.table {
  position: relative;

  .p-sticky {
    top: rem(66);

    th {
      padding-bottom: rem(7);
      background-color: $white;
      border-bottom: rem(1) solid $blue;
    }
  }

  thead {
    border-bottom: rem(1) solid $black-transparent-2;
  }

  tbody tr:first-child td {
    padding-top: rem(10);
  }
}

