caption {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  color: #B5B5C3;
  text-align: left;
  caption-side: bottom;
}

th {
  text-align: inherit;
  text-align: -webkit-match-parent;
}

[hidden] {
  display: none !important;
}

h1, h2, h3, h4, h5, h6 {
  margin-bottom: 0;
  margin-top: 0;
}

hr {
  margin-top: 1rem;
  margin-bottom: 1rem;
  border: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

small,
.small {
  font-size: 80%;
  font-weight: 400;
}

mark,
.mark {
  padding: 0.2em;
  background-color: #fcf8e3;
}

code {
  font-size: 87.5%;
  color: #e83e8c;
  word-wrap: break-word;
}

a > code {
  color: inherit;
}

kbd {
  padding: 0.2rem 0.4rem;
  font-size: 87.5%;
  color: #ffffff;
  background-color: #181C32;
  border-radius: 0.28rem;
  box-shadow: inset 0 -0.1rem 0 rgba(0, 0, 0, 0.25);
}

kbd kbd {
  padding: 0;
  font-size: 100%;
  font-weight: 500;
  -webkit-box-shadow: none;
  box-shadow: none;
}

pre {
  display: block;
  font-size: 87.5%;
  color: #181C32;
}

pre code {
  font-size: inherit;
  color: inherit;
  word-break: normal;
}

#installContainer {
  font-size: rem(13);
  padding: rem(8);

  p {
    margin-bottom: 0;
    margin-right: rem(5);
  }

  #butInstall {
    font-size: rem(14);
    color: $white;
    line-height: 1.1;
    border-radius: rem(25);
    border: 0;
    padding: rem(5) rem(20);
    background-color: $yellow;
  }
}

.b-top {
  border-top: 1px solid $black-transparent-2;
}

.b-bottom {
  border-bottom: 1px solid $black-transparent-2;
}
