input,
button,
select,
optgroup,
textarea {
  font-family: $font-family;
}

label {
  display: inline-block;
  margin-bottom: 0.5rem;
}

button {
  border-radius: 0;
}

button:focus:not(:focus-visible) {
  outline: 0;
}

a, button, input[type="submit"], input[type="button"] {
  &:focus-visible {
    outline: rem(2) solid $green;
    outline-offset: rem(2);
  }
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

[role=button] {
  cursor: pointer;
}

select {
  word-wrap: normal;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button:not(:disabled),
[type=button]:not(:disabled),
[type=reset]:not(:disabled),
[type=submit]:not(:disabled) {
  cursor: pointer;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type=radio],
input[type=checkbox] {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  outline-offset: -2px;
  -webkit-appearance: none;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

input[readonly] {
  background-color: $white-transparent-5;
}

.form-control:focus::-webkit-input-placeholder {
  color: transparent;
}

.form-control:focus::-moz-placeholder {
  color: transparent;
}

.form-control:focus:-moz-placeholder {
  color: transparent;
}

.form-control:focus:-ms-input-placeholder {
  color: transparent;
}

.form-control[disabled], fieldset[disabled] .form-control,
.form-control-select[disabled], fieldset[disabled] .form-control-select,
.form-control-textarea[disabled], fieldset[disabled] .form-control-textarea {
  background-color: $white-transparent-5 !important;
  background-image: none !important;
  color: $white-transparent-5;
}

output {
  display: inline-block;
}

summary {
  display: list-item;
  cursor: pointer;
}

.btn {
  color: $white;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: 0;
  position: relative;

  &:active {
    box-shadow: inset 0 0 0 0.2rem $white-transparent-4;

  }

  &:focus {
    box-shadow: 0 rem(5) rem(5) rem(-3) rgb(0 0 0 / 20%), 0 rem(8) rem(10) rem(1) rgb(0 0 0 / 14%), 0 rem(3) rem(14) rem(2) rgb(0 0 0 / 12%);
  }
}

.btn-block {
  width: 100%;
  display: flex;
}

.btn-warning {
  background-color: $yellow;
}

.btn-info {
  background-color: $blue;
}

.btn-success {
  background-color: $green;
}

.btn-lg {
  font-size: 1.4rem;
  font-weight: $font-semi-bold;
  text-transform: uppercase;
  height: rem(34);
  border-radius: rem(25);
}

.btn-m {
  height: rem(30);
}

.btn {
  .svg-icon {
    fill: currentColor;
    position: absolute;
  }

  .svg-right {
    right: rem(10);
  }

  .svg-left {
    left: rem(10)
  }

  .svg-user, .svg-plus {
    width: rem(18);
    height: rem(18);
  }

  .svg-check {
    width: rem(17);
    height: rem(13);
  }

}

//////////////////////

.form-group, .form-action {
  margin-bottom: rem(12);
}

.form-control {
  font-size: rem(13);
  font-weight: $font-normal;
  line-height: 1.7;
  color: $black;
  box-shadow: 0 rem(2) rem(6) 0 $black-transparent-1, inset 0 rem(2) rem(6) 0 $black-transparent-1;
  border: 1px solid $black-transparent-2;
  caret-color: $green;
  padding-left: rem(12);
  padding-right: rem(12);
  width: 100%;
  height: rem(34);

  @media screen and (-webkit-min-device-pixel-ratio: 0) {
    font-size: 16px;
  }

  @include input-placeholder {
    color: $black-transparent-3;
    opacity: 1;
  }

  overflow: visible;
  display: block;
  background-color: $white;

  &:active, &:focus {
    box-shadow: rem(2) rem(2) rem(3) $black-transparent-5;
    outline: none;
  }
}

//////////////

.input-icon.input-icon-right {
  .form-control {
    padding-right: rem(30);
  }
}

.input-icon.input-icon-right span {
  right: 0;
  left: auto;
}

.input-icon.input-icon-left {
  .form-control {
    padding-left: rem(35);
  }
}

.input-icon.input-icon-left span {
  left: 0;
}

.input-icon {
  position: relative;
  display: block;

  span {
    top: 0;
    bottom: 0;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    width: calc(1.5em + 1.3rem + 2px);
  }

  .svg-envelop, .svg-unlock, .svg-user {
    width: rem(20);
    height: rem(20);
    stroke: $black-transparent-2;
  }
}

////// login pages

.login-pages {
  @extend .bg-secondary;
  font-size: 13px;
}

.form-login {
  padding-top: rem(20);

  .form-control {
    height: rem(40);
  }

  .btn-m {
    height: rem(40);
  }
}

.form-login__title {
  text-align: center;
  text-transform: uppercase;
  color: $black;
  font-weight: $font-semi-bold;
  font-size: rem(18);
  margin-bottom: rem(20);

  span {
    text-transform: none;
  }
}

